@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@300;500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@300;500;800&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.2/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Montserrat', sans-serif;
}

.login-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.login-form, .login-image {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 50%;
}

.login-image {
  background-color: rgb(202, 210, 197);
}

.login-form p{
  color: rgb(53, 79, 82);
  font-family: 'Bebas Neue', cursive;
  font-size: 3em;
  letter-spacing: 0.5em;
  margin: 0;
  margin-bottom: 5%;
}

.login-form input{
  width: 48%;
  height: 40px;
  margin: 2% 0;
  border: none;
  border-bottom: 1px solid rgb(47, 62, 70);
}

.login-form input:focus {
  border: none;
  border-bottom: 2px solid rgb(47, 62, 70);
  background-color: rgb(243, 243, 243);
  outline: none;
}

.login-form button {
  background-color: rgb(53, 79, 82);
  border: none;
  border-radius: 0;
  color: white;
  cursor: pointer;
  font-family: 'Bebas Neue', cursive;
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  margin-top: 3%;
  padding: 1.3%;
  width: 48%;
}

.login-form button:disabled {
  background-color: rgb(235, 235, 235);
  border:  none;
  color: white;
  cursor: auto;
}

.login-image img {
  width: 60%;
}
*{
  font-family: 'Montserrat', sans-serif;
}

.form-table-container {
  display: flex;
  flex-direction: row;
}

form {
  border: 1px solid rgb(243, 243, 243);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 5%;
  padding: 10%;
  width: 45vh;
}

form, form label {
  display: flex;
  flex-direction: column;
  margin-top: 8%;
}

form label input{
  width: 100%;
  height: 40px;
  margin: 2% 0;
  border: none;
  border-bottom: 1px solid rgb(47, 62, 70);
}

select {
  background-color: rgb(243, 243, 243);
  width: 100%;
  height: 40px;
  margin: 2% 0;
  border: none;
}

select:focus {
  border: none;
  outline: none;
}

form label input:focus {
  border: none;
  border-bottom: 2px solid rgb(47, 62, 70);
  background-color: rgb(243, 243, 243);
  outline: none;
}

.form-btn {
  background-color: rgb(53, 79, 82);
  border: none;
  border-radius: 0;
  color: white;
  cursor: pointer;
  font-family: 'Bebas Neue', cursive;
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  margin-top: 10%;
  padding: 3%;
  width: 100%;
}
*{
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

.header-container {
  align-items: center;
  background-color: rgb(132, 169, 140);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1.6%;
}

.header-title {
  font-family: 'Bebas Neue', cursive;
  font-size: 2em;
  letter-spacing: 0.4em;
}

.header-user-info {
  display: flex;
  flex-direction: row;
  letter-spacing: 0.1rem;
  margin-right: 10%;
}

.header-user-info p:nth-child(1) {
  margin-right: 40%;
}
*{
  font-family: 'Montserrat', sans-serif;
}

.table-container{
  width: 70%;
}

table { 
  border: 1px solid rgb(243, 243, 243);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 3% 3% 3% 6%;
  padding: 2%;
  width: 100%;
}

th {
  color: rgb(53, 79, 82);
  font-size: 1em;
  font-weight: 400;
  padding: 1%;
  text-transform: uppercase;
  width: 200px;
}

th:nth-child(odd) {
  background-color: rgb(243, 243, 243)
}

td {
  font-size: 0.8rem;
  height: 50px;
  text-align: center;
}

tr:nth-child(odd) {
  background-color: rgb(243, 243, 243)
}

td button {
  background-color: transparent;
  border: none;
}

.bi-trash {
  color: rgb(47, 62, 70);
  font-size: 20px;
}

.bi-trash:hover {
  color: rgb(193, 18, 31);
  cursor: pointer;
}

